import React from "react";
import UserSpace from "../../templates/UserSpace";
import { graphql } from "gatsby";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import ProtectedRoute from "../../components/protectedRoute";
import Login from "../login";
import EbookEmbed from "../../templates/ebook";

interface UserPageProps {
  userId: string;
  useruid: string;
  language: string;
  locales: {
    ns: string;
    data: string;
    language: string;
  }[];
}

const UserPage = ({ pageContext }: { pageContext: UserPageProps }) => {
  return (
    <ProtectedRoute>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path="/user/:useruid" element={<UserSpace pageContext={pageContext} />} />
          <Route path="/user/:useruid/ebook" element={<EbookEmbed pageContext={pageContext} />} />
        </Routes>
      </BrowserRouter>
    </ProtectedRoute>
  );
};

export default UserPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
