import React from 'react';
import { useParams } from 'react-router-dom';
import Layout from '../pages/layout';
import { Company } from "../interfaces";
import data from "../data/data.json";
import { useNavigate } from 'react-router-dom';
import { signOut } from "firebase/auth";
import { auth } from "../firebaseConfig";
import { useTranslation } from "gatsby-plugin-react-i18next";
import {  Link } from 'gatsby';
import FileForm from '../components/fileForm';

const info: Company = data[0];


const UserSpace = ( { pageContext }: { pageContext: any }) => {
  const { useruid } = useParams();  // get the userId from URL
  const navigate = useNavigate();
  const { t } = useTranslation();

  const handleLogout = async () => {
    try {
      await signOut(auth);
      navigate("/");
    } catch (error) {
      console.error("Error al cerrar sesin:", error);
      alert("Error al cerrar sesin. Intenta nuevamente.");
    }
  };

  return (
    <Layout data={info}>
      <div className="p-6 bg-gray-100 rounded-lg shadow-md text-dark">
        <h1 className="text-2xl font-bold mb-4">{t("userInfo.title")}</h1>
        <FileForm pageContext={pageContext} />
        <button onClick={handleLogout} className="mt-4 bg-red-500 text-white p-2 rounded">{t("logout")}</button>
        <Link to={`/user/${useruid}/ebook`}>
          <button className="text-dark btn-primary">Acceder al Ebook</button>
        </Link>
      </div>
    </Layout>
  );
};

export default UserSpace;
