import React, { useEffect, useState } from "react";
import { navigate } from "gatsby";
import { auth, db } from "../firebaseConfig";
import { doc, getDoc } from "firebase/firestore";

const ProtectedRoute = ({ children }: { children: React.ReactNode }) => {
  const [loading, setLoading] = useState(true);
  const [isAllowed, setIsAllowed] = useState(false);

  useEffect(() => {
    const checkApproval = async () => {
      const user = auth.currentUser;
      if (!user) {
        navigate("/login" as unknown as number); // Redirige al inicio de sesin si no hay usuario
        return;
      }
      try {
        const userDoc = await getDoc(doc(db, "users", String(user.uid))); // Asegrate de convertir user.uid a string
        if (userDoc.exists() && Boolean(userDoc.data()?.isApproved)) {
          setIsAllowed(true);
        } else {
          navigate("/not-approved" as unknown as number); // Redirige si el usuario no est aprobado
        }
      } catch (error) {
        console.error("Error al verificar la aprobacin del usuario:", error);
        navigate("/login" as unknown as number); // Redirige en caso de error
      } finally {
        setLoading(false);
      }
    };

    checkApproval();
  }, [navigate]);

  if (loading) return <p>Cargando...</p>;
  if (!isAllowed) return null;

  return <>{children}</>;
};

export default ProtectedRoute;
