import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { doc, getDoc, setDoc, Timestamp } from "firebase/firestore";
import { db, auth } from "../firebaseConfig";
import { graphql, navigate } from "gatsby";
import { onAuthStateChanged } from "firebase/auth";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
} from "@mui/material";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import dayjs from "dayjs";
import "dayjs/locale/es";

dayjs.locale("es");

interface UserData {
    fullName: string;
    birthDate: Date | null;
    birthPlace: string;
    maritalStatus: string;
    children: string;
    childrenAmount: number;
    address: string;
    city: string;
    state: string;
    postalCode: string;
    country: string;
    phone: string;
    email: string;
    profession: string;
    general: string;

    isApproved?: boolean;
    role?: string;
    timestamp?: Timestamp;
}

type ErrorState = {
  fullName: string;
  birthDate: Date | null;
  birthPlace: string;
  maritalStatus: string;
  children: string;
  childrenAmount: number;
  address: string;
  city: string;
  state: string;
  postalCode: string;
  country: string;
  phone: string;
  email: string;
  profession: string;
  general: string;
};

const colors = {
  dark: "#8C4A7B", // purple
  primary: "#403B38", // gray darker
  secondary: "#BF8A8A", // pink
  terciary: "#D9D0D0", // gray mid
  light: "#F2F2F2", // gray light
  semiLight: "#cccccc", // gray
};

const textFieldStyles = {
  "& .MuiFormLabel-root": { color: colors.dark },
  "& .MuiInput-underline:after": { borderBottomColor: colors.dark },
  "& .MuiInputBase-input": { color: colors.secondary },
  "& .MuiFormLabel-root.Mui-focused": { color: colors.dark },
  "& .MuiFilledInput-root:before": { borderBottomColor: colors.dark },
};

const FileForm = ({ pageContext }: { pageContext: { userId: string } }) => {
  const { t } = useTranslation();
  const { useruid } = useParams(); // Obtener el userId de la URL
  const [userData, setUserData] = useState<UserData | null>(null); // Almacena datos del usuario de Firestore
  const [answers, setAnswers] = useState<Record<string, string>>({}); // Almacena respuestas del formulario
  const [showDataPanel, setShowDataPanel] = useState(true); // Estado para alternar entre el formulario y el panel de datos

  const [formData, setFormData] = useState({
    fullName: "",
    birthDate: null as Date | null,
    birthPlace: "",
    maritalStatus: "",
    children: "",
    childrenAmount: 0,
    address: "",
    city: "",
    state: "",
    postalCode: "",
    country: "",
    phone: "",
    email: "",
    profession: "",
  });

  const [error, setError] = useState<ErrorState>({
    fullName: "",
    birthDate: null,
    birthPlace: "",
    maritalStatus: "",
    children: "",
    childrenAmount: 0,
    address: "",
    city: "",
    state: "",
    postalCode: "",
    country: "",
    phone: "",
    email: "",
    profession: "",
    general: "",
  });

  // Maneja cambios en los campos del formulario
  const handleAnswerChange = (
    e: React.ChangeEvent<HTMLInputElement>,
    question: string
  ) => {
    setAnswers((prevAnswers) => ({
      ...prevAnswers,
      [question]: e.target.value,
    }));
  };

  // Carga los datos del usuario desde Firestore
  const fetchUserData = async () => {
    if (useruid) {
      try {
        const userDocRef = doc(db, "users", useruid);
        const userDoc = await getDoc(userDocRef);
  
        if (userDoc.exists()) {
          const data = userDoc.data() as UserData;
          setUserData(data);
          setFormData({
            ...data,
            birthDate: data.birthDate instanceof Timestamp ? data.birthDate.toDate() : null,
          });
        } else {
          console.error("No se encontr el documento del usuario.");
        }
      } catch (error) {
        console.error("Error al obtener los datos del usuario:", error);
      }
    }
  };
  

  // Enviar el formulario
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const formattedDate = dayjs(formData.birthDate).format("D [de] MMMM [de] YYYY");
    if (!useruid || !userData) {
      console.error(
        "El ID del usuario o los datos del usuario no estn definidos."
      );
      return;
    }

        // Verifica si todos los campos requeridos estn diligenciados
        const isComplete = [
            "fullName",
            "birthDate",
            "birthPlace",
            "maritalStatus",
            "address",
            "city",
            "state",
          ].every((field) => formData[field as keyof typeof formData]);
      
          if (isComplete) {
            setShowDataPanel(true); // Cambia al panel de datos
          } else {
            alert(t("forms.fileForm.incompleteFields")); // Muestra un mensaje si hay campos incompletos
          }
  
    try {
      const userDocRef = doc(db, "users", useruid);
      await setDoc(userDocRef,      {
        ...userData,
        ...formData,
        birthDate: formData.birthDate ? Timestamp.fromDate(formData.birthDate) : null,
        timestamp: Timestamp.now(),
      }, { merge: true });
    } catch (error) {
      console.error("Error al actualizar los datos del usuario:", error);
    }
  };
  

  // Verificar autenticacin del usuario y cargar datos
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (!user) {
        navigate("/login");
      } else {
        fetchUserData();
      }
    });

    return () => unsubscribe();
  }, [useruid]);

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setError((prevError) => ({ ...prevError, [name]: "" }));
  };

  const handleSelectChange = (event: SelectChangeEvent<string>) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value, }));
  };

  const handleEdit = () => {
    setShowDataPanel(false); // Vuelve a mostrar el formulario para edicin
  };

  return (
    <>
    {!showDataPanel ? (
      <Box
        component="form"
        sx={{ "& > :not(style)": { mt: 4, width: "100%" } }}
        noValidate
        autoComplete="off"
        onSubmit={handleSubmit}
        className={styles.form}
      >
        {error.general && (
          <p className={styles.errorMessage}>{error.general}</p>
        )}
        <TextField
          id="fullName"
          label={t("forms.fileForm.fullName")}
          variant="standard"
          sx={textFieldStyles}
          name="fullName"
          value={formData.fullName || ""}
          onChange={handleInputChange}
          required
        />

        <div className="flex gap-4">
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DatePicker
                className="w-full !-mt-2"
                label={t("forms.fileForm.birthDate")}
                views={["month", "day", "year"]}
                format="MMMM/DD/YYYY"
                onChange={(newValue) => {
                    setFormData((prevFormData) => ({
                    ...prevFormData,
                    birthDate: newValue ? newValue.toDate() : null,
                    }));
                }}
                value={formData.birthDate ? dayjs(formData.birthDate) : null}
              sx={{
                "& .MuiFormLabel-root": { color: colors.dark },
                "& .MuiInput-underline:after": {
                  borderBottomColor: colors.dark,
                },
                "& .MuiFilledInput-root": { backgroundColor: "transparent" },
                "& .MuiFilledInput-root:before": {
                  borderBottomColor: colors.dark,
                },
                "& .MuiInputBase-input": { color: colors.secondary },
              }}
              slotProps={{
                openPickerIcon: { fontSize: "medium" },
                openPickerButton: { color: "secondary" },
                textField: {
                  variant: "filled",
                  focused: false,
                  color: "secondary",
                },
              }}
            />
          </LocalizationProvider>

          <TextField
            className="w-full"
            id="birthPlace"
            label={t("forms.fileForm.birthPlace")}
            variant="standard"
            sx={textFieldStyles}
            name="birthPlace"
            value={formData.birthPlace || ""}
            onChange={handleInputChange}
            required
          />
        </div>
        <div className="flex gap-4">
          <FormControl fullWidth>
            <InputLabel>{t("forms.fileForm.maritalStatus")}</InputLabel>
            <Select
              variant="standard"
              labelId="demo-simple-select-label"
              value={formData.maritalStatus}
              label={t("forms.fileForm.maritalStatus")}
              onChange={handleSelectChange}
              name="maritalStatus"
              sx={textFieldStyles}
              required
            >
              <MenuItem value="single">
                {t("forms.fileForm.maritalStatusValues.single")}
              </MenuItem>
              <MenuItem value="married">
                {t("forms.fileForm.maritalStatusValues.married")}
              </MenuItem>
              <MenuItem value="separated">
                {t("forms.fileForm.maritalStatusValues.separated")}
              </MenuItem>
              <MenuItem value="divorced">
                {t("forms.fileForm.maritalStatusValues.divorced")}
              </MenuItem>
              <MenuItem value="widowed">
                {t("forms.fileForm.maritalStatusValues.widowed")}
              </MenuItem>
            </Select>
          </FormControl>

          <FormControl fullWidth>
            <InputLabel>{t("forms.fileForm.children")}</InputLabel>
            <Select
              variant="standard"
              labelId="demo-simple-select-label"
              value={formData.children}
              label={t("forms.fileForm.children")}
              onChange={handleSelectChange}
              name="children"
              sx={textFieldStyles}
              required
            >
              <MenuItem value="yes">
                {t("forms.fileForm.childrenValues.yes")}
              </MenuItem>
              <MenuItem value="no">
                {t("forms.fileForm.childrenValues.no")}
              </MenuItem>
            </Select>
          </FormControl>

          {formData.children === 'yes' && (
            <TextField
              className="w-full"
              id="childrenAmount"
              label={t("forms.fileForm.childrenAmount")}
              variant="standard"
              sx={textFieldStyles}
              name="childrenAmount"
              value={formData.childrenAmount || ""}
              onChange={handleInputChange}
              required
            />
          )}
        </div>

        <div className="flex gap-4">
          <TextField
            className="w-full"
            id="address"
            label={t("forms.fileForm.address")}
            variant="standard"
            sx={textFieldStyles}
            name="address"
            value={formData.address || ""}
            onChange={handleInputChange}
            required
          />
          <TextField
            className="w-full"
            id="city"
            label={t("forms.fileForm.city")}
            variant="standard"
            sx={textFieldStyles}
            name="city"
            value={formData.city || ""}
            onChange={handleInputChange}
            required
          />
          <TextField
            className="w-full"
            id="state"
            label={t("forms.fileForm.state")}
            variant="standard"
            sx={textFieldStyles}
            name="state"
            value={formData.state || ""}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="flex gap-4">
          <TextField
            className="w-full"
            id="postalCode"
            label={t("forms.fileForm.postalCode")}
            variant="standard"
            sx={textFieldStyles}
            name="postalCode"
            value={formData.postalCode || ""}
            onChange={handleInputChange}
          />
          <TextField
            className="w-full"
            id="country"
            label={t("forms.fileForm.country")}
            variant="standard"
            sx={textFieldStyles}
            name="country"
            value={formData.country || ""}
            onChange={handleInputChange}
          />
          <TextField
            className="w-full"
            id="phone"
            label={t("forms.fileForm.phone")}
            variant="standard"
            sx={textFieldStyles}
            name="phone"
            value={formData.phone || ""}
            onChange={handleInputChange}
          />
        </div>

        <div className="flex gap-4">
          <TextField
            className="w-full"
            id="email"
            label={t("forms.fileForm.email")}
            variant="standard"
            sx={textFieldStyles}
            name="email"
            value={formData.email || ""}
            onChange={handleInputChange}
          />
          <TextField
            className="w-full"
            id="profession"
            label={t("forms.fileForm.profession")}
            variant="standard"
            sx={textFieldStyles}
            name="profession"
            value={formData.profession || ""}
            onChange={handleInputChange}
          />
        </div>

        <Button
          type="submit"
          variant="contained"
          className="capitalize"
          sx={{ backgroundColor: colors.dark, color: colors.light }}
        >
          {t("forms.fileForm.submit")}
        </Button>
      </Box>
          ) : (
            <Box className="">
              <p>
                <strong>{t("forms.fileForm.fullName")}: </strong>
                {formData.fullName}
              </p>
              <p>
                <strong>{t("forms.fileForm.birthDate")}: </strong>
                {formData.birthDate ? dayjs(formData.birthDate).format("D [de] MMMM [de] YYYY") : ""}
                  
              </p>
              <p>
                <strong>{t("forms.fileForm.birthPlace")}: </strong>
                {formData.birthPlace}
              </p>
              <p>
                <strong>{t("forms.fileForm.maritalStatus")}: </strong>
                {formData.maritalStatus}
              </p>
              <p>
                <strong>{t("forms.fileForm.children")}: </strong>
                {formData.children}
              </p>
              <p>
                <strong>{t("forms.fileForm.childrenAmount")}: </strong>
                {formData.childrenAmount}
              </p>
              <p>
                <strong>{t("forms.fileForm.address")}: </strong>
                {formData.address}
              </p>
              <p>
                <strong>{t("forms.fileForm.city")}: </strong>
                {formData.city}
              </p>
              <p>
                <strong>{t("forms.fileForm.state")}: </strong>
                {formData.state}
              </p>
              <p>
                <strong>{t("forms.fileForm.postalCode")}: </strong>
                {formData.postalCode}
              </p>
              <p>
                <strong>{t("forms.fileForm.country")}: </strong>
                {formData.country}
              </p>
              <p>
                <strong>{t("forms.fileForm.phone")}: </strong>
                {formData.phone}
              </p>
              <p>
                <strong>{t("forms.fileForm.email")}: </strong>
                {formData.email}
              </p>
              <p>
                <strong>{t("forms.fileForm.profession")}: </strong>
                {formData.profession}
              </p>
    
              <Button 
                variant="contained"
                className="capitalize"
                sx={{ backgroundColor: colors.dark, color: colors.light }}
                onClick={handleEdit}>
                {t("forms.fileForm.edit")}
              </Button>
            </Box>
          )}  
    </>
  );
};

export default FileForm;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  form: "flex flex-col w-full sm:w-1/3 px-2 sm:px-12",
  errorMessage: "text-red-600 text-xs",
  thankYouMessage: "text-center text-lg text-orange self-center",
};
